.video__image-wrapper{position:relative}.video__image{max-width:100%;max-height:100%}.video__play{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%,-50%);-ms-transform:translate(-50%,-50%);transform:translate(-50%,-50%);display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;width:50px;height:50px;border:1px solid #00bfbf;border-radius:50%;background:rgba(0,0,0,.6);-webkit-box-shadow:0 0 20px 0 rgba(0,0,0,.77);box-shadow:0 0 20px 0 rgba(0,0,0,.77)}.video__play-icon{margin-left:4px}