.input{position:relative;min-height:40px;width:100%;border:1px solid #000;border-radius:12px;padding:10px 12px;font-family:Roboto,sans-serif;font-size:14px}.input::-webkit-input-placeholder{font-size:14px;color:#859ea3}.input:-moz-placeholder,.input::-moz-placeholder{font-size:14px;color:#859ea3}.input:-ms-input-placeholder{font-size:14px;color:#859ea3}.textarea{position:relative;min-height:300px;width:100%;border:1px solid #000;border-radius:12px;padding:10px 12px;font-family:Roboto,sans-serif;font-size:14px}.textarea::-webkit-input-placeholder{font-size:14px;color:#859ea3}.textarea:-moz-placeholder,.textarea::-moz-placeholder{font-size:14px;color:#859ea3}.textarea:-ms-input-placeholder{font-size:14px;color:#859ea3}@media (max-width:599px){.textarea{min-height:150px}}