.menu-page{list-style:none;margin-bottom:40px}.menu-page--center{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center}.menu-page li{position:relative;display:inline-block;margin-bottom:10px}.menu-page li:not(:last-child){margin-right:20px}@media (min-width:768px){.menu-page li:not(:last-child){margin-right:calc(20px + (3000vw - 23040px)/256)}}@media (min-width:1024px){.menu-page li:not(:last-child){margin-right:50px}}.menu-page li:hover a{color:#fff}.menu-page li:hover:before{height:100%;width:calc(100% + 30px)}.menu-page li:before{position:absolute;bottom:0;left:50%;content:"";width:100%;height:4px;border-radius:10px;background-color:#0fc9d0;-webkit-transition:all .08s ease-out;-o-transition:all .08s ease-out;transition:all .08s ease-out;-webkit-transform:translate(-50%);-ms-transform:translate(-50%);transform:translate(-50%)}.menu-page a{position:relative;font-size:18px;font-weight:500;color:#00bfbf;padding:9px 0;display:inline-block;-webkit-transition:all .1s ease-out;-o-transition:all .1s ease-out;transition:all .1s ease-out}