.breadcrumbs{-ms-flex-wrap:wrap;flex-wrap:wrap;list-style:none;margin-bottom:10px}.breadcrumbs,.breadcrumbs li{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.breadcrumbs li{position:relative}.breadcrumbs li:not(:first-child):before{min-width:4px;min-height:4px;display:inline-block;content:"";background:#fff;margin-right:10px}.breadcrumbs li:not(:last-child){margin-right:10px}.breadcrumbs li:hover a{color:#5edaf1}.breadcrumbs a{font-size:18px;font-weight:700;color:var(--green-color);-webkit-transition:all .2s ease-out;-o-transition:all .2s ease-out;transition:all .2s ease-out}